.container-library {
    display: grid;
    grid-template-columns: 1fr 655px 1fr;
    grid-template-rows: 30px auto auto auto;
    grid-row-gap: 5px;
    grid-template-areas:
            ". . ."
            ". library .";
    -ms-flex-pack: center;
        justify-content: center;
}

.item-channels {
    grid-area: library;
}


