.container-channel {
    display: grid;
    grid-template-columns: 1fr 655px 1fr;
    grid-template-rows: 30px auto auto auto;
    grid-row-gap: 5px;
    grid-template-areas:
            ". . ."
            ". channel .";
    -ms-flex-pack: center;
        justify-content: center;
}

.item-channel {
    grid-area: channel;
}



